.card {
  position: relative;
  overflow: hidden;
  margin-bottom: 60px;
  border-bottom: 1px solid #c9c9c9;
  border-right: 1px solid #e4e4e4;
  border-left: 1px solid #e4e4e4;
  border-radius: 4px; }
  .card__update, .card__video-count {
    position: absolute;
    top: 5px;
    letter-spacing: 0.2px;
    z-index: 1; }
  .card__update {
    left: 12px;
    text-transform: uppercase; }
  .card__video-count {
    right: 12px;
    color: #FFF;
    font-weight: bold; }
  .card__img {
    position: relative; }
    .card__img:hover .card__overlay {
      background-color: rgba(0, 0, 0, 0.7);
      opacity: .9; }
    .card__img:hover .card__overlay > i {
      transform: scale(1); }
  .card__overlay {
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    background-color: transparent;
    transition: all 0.32s ease-in-out;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0; }
    .card__overlay > i {
      font-size: 60px;
      color: #FFF;
      transform: scale(1.2);
      transition: all 0.32s ease-in-out; }
  .card__details {
    background-color: #FFF;
    padding: 8px 12px 10px; }
  .card__category {
    margin: 0;
    padding: 4px 0;
    text-align: left; }
  .card__title {
    margin: 0;
    padding-top: 4px;
    font-size: 15px;
    text-align: left; }
  .card__title--a {
    color: #576366; }
    .card__title--a:focus, .card__title--a:hover {
      color: #343b3d; }

